
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'

import Tag from 'c%/Tag'
import Card from 'vux/src/components/card/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'


export default{
mixins: [ common ],
components:{
Tag,
Card,
Cell,
XInput,
Checker,
XButton,
XTextarea,
CheckerItem,
PopupPicker
},
data () {
return {
students: [],
classes: [],
growth: {
bid: 0,
cid: [],
sid: [],
title: '',
ability_ids: [],
content: ''
}
}
},
watch: {
'growth.cid': function (val) {
if(val.length > 0){
let cid = val[0]
this.get_students(cid)
}
}
},
mounted () {
this.get_classes()
},
methods: {
choose_abilities(item) {
let [id,ability_ids] = [item.id,this.growth.ability_ids]
if(ability_ids.indexOf(id)>-1){
this.growth.ability_ids.splice(ability_ids.indexOf(id),1)
}else{
this.growth.ability_ids.push(id)
}
},
get_classes () {
let uri = util.sprintf('classes?teach_eid=%s&my=true',this.eid$)

this.$rest(uri).get()
.success(data=>{
let list = data.list
if(list.length > 0) {
this.classes = []
list.forEach(c=>{
this.classes.push({
value: c.cid,
name: c.class_name
})
})
}
}).error(response=>{
this.toast(response.body.message,'text')
})
},
get_students (cid) {
this.$rest('classes').add_url_param(cid,'students').get()
.success(data=>{
let list = data.list
this.students = []
if(list.length > 0){
list.forEach(s=>{
this.students.push({
name: s.student.student_name,
value: parseInt(s.sid)
})
})
}else{
this.toast('该班级没有学员','text')
}
}).error(response=>{
this.toast(response.body.message,'text')
})
},
format_student (val) {
let tem_student = this.students.find(s=>s.value == val)
if(tem_student) {
return tem_student.name
}
return ''
},
format_class (val) {
let tem_class = this.classes.find(c=>c.value==val)
if(tem_class){
return tem_class.name
}
return ''
},
submit_growth () {
let params = util.copy(this.growth)
params.cid = params.cid[0]
params.sid = params.sid[0]
params.bid = this.bid$[0]
this.$rest('edu_growups').post(params)
.success(response=>{
this.toast('添加成功','success')
setTimeout(()=>{
this.$router.push({path: './growth_contrast'})
},800)
}).error(response=>{
this.toast(response.body.message,'text')
})
},
},
computed: {
abilities () {
let [lesson_config,ability] = [this.$store.state.gvars.lesson_config,[]]
if(lesson_config && lesson_config.ability){
ability = lesson_config.ability
}
return ability
}
}
}



